* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

div {
  padding: 15px 0;
}

form {
  width: 450px;
  margin: 0 auto;
  position: relative;
}

.form-wrap {
  background-color: #00bfff;
}

.form-wrap input {
  width: 100%;
  height: 42px;
  color: #000;
  background: #f9f0da;
  border: 2px solid #20b2aa;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
}

.form-wrap button {
  width: 58px;
  height: 42px;
  cursor: pointer;
  background: #20b2aa;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 0;
  right: 0;
}

.form-wrap button:before {
  content: "Search";
  color: #f9f0da;
  font-family: FontAwesome;
  font-size: 16px;
}

.load-more {
  color: #f9f0da;
  width: 100px;
  height: 42px;
  cursor: pointer;
  background: #20b2aa;
  border: none;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  font-family: FontAwesome;
  display: block;
}

